<template>
  <v-container v-scroll="scroll">
    <v-layout wrap>
      <slot/>

      <v-flex xs12 text-xs-center :style="{ visibility: loading ? 'visible' : 'hidden' }" class="pa-5">
        <v-progress-circular color="primary" :indeterminate="true"/>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  props: {
    loading: { type: Boolean, required: true }
  },
  watch: {
    loading (val) {
      if (!val) {
        window.setTimeout(() => {
          this.scroll()
        }, 50)
      }
    }
  },
  methods: {
    scroll () {
      if (
        !this.loading && (
          document.documentElement.offsetHeight - 256 <=
          document.documentElement.scrollTop + window.innerHeight
        )
      ) {
        this.$emit('load')
      }
    }
  },
  mounted () {
    this.$emit('load')
    window.addEventListener('resize', this.scroll)
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.scroll)
  }
}
</script>
