import { build } from './builder'

const urlPrefix = '/subject'
const polyfill = {
  test: (v, search) => v.name.toLowerCase().includes(search) ||
    v.email.toLowerCase().includes(search) ||
    v.telephone.toLowerCase().includes(search) ||
    v.ico.toLowerCase().includes(search),
  uniqProp: 'subjectId'
}

const {
  search: searchSubjects,
  get: getSubjects,
  post: postSubject,
  put: putSubject,
  delete: deleteSubject
} = build(urlPrefix, polyfill)

export { searchSubjects, getSubjects, postSubject, putSubject, deleteSubject }
