<template>
  <v-form ref="form" v-model="valid">
    <DialogWrapper :headline="$t('car')" :value="show" :saveBtn="valid" @save="save" @cancel="cancel">
      <v-flex xs12>
        <v-text-field :label="$t('car-number')" v-model="carNumber" :rules="[rules.required, rules.carNumber]"/>
      </v-flex>
      <v-flex xs12>
        <v-text-field :label="$t('name')" v-model="item.name" clearable/>
      </v-flex>
      <v-flex xs12>
        <RemoteSelect
          :label="$t('owner')"
          :rules="[rules.required]"
          :searchItems="searchSubjects"
          item-text="name"
          item-value="subjectId"
          v-model="item.owner"
        />
      </v-flex>
      <v-flex xs12>
        <RemoteSelect
          :label="$t('serie')"
          :rules="[rules.required]"
          :searchItems="searchSeries"
          item-text="serieId"
          item-value="serieId"
          v-model="item.serie"
        />
      </v-flex>
      <v-flex xs12>
        <RemoteSelect
          :label="$t('service-responsible-person')"
          :rules="[rules.required]"
          :searchItems="searchSubjects"
          item-text="name"
          item-value="subjectId"
          v-model="item.serviceResponsiblePerson"
        />
      </v-flex>
      <v-flex xs12>
        <v-switch :label="$t('certification')" v-model="item.certification" :rules="[rules.required]"/>
      </v-flex>
      <v-flex xs12>
        <v-text-field :label="$t('revision-period')" v-model.number="item.revisionPeriod" type="number" :suffix="$t('units.days', [''])" :rules="[rules.required, rules.nonnegative, rules.integer]"/>
      </v-flex>
      <v-flex xs12>
        <DateTime :label="$t('last-revision')" v-model="item.lastRevision" :rules="[rules.required, rules.past]"/>
      </v-flex>
      <v-flex xs12>
        <DateTime :label="$t('last-ZTE')" v-model="item.lastZTE" :rules="[rules.required, rules.past]"/>
      </v-flex>
      <v-flex xs12>
        <DateTime :label="$t('last-ZTL')" v-model="item.lastZTL" :rules="[rules.required, rules.past]"/>
      </v-flex>
    </DialogWrapper>
  </v-form>
</template>

<script>
import DateTime from '@/components/DateTime'
import DialogMixin from './DialogMixin'
import RemoteSelect from '@/components/RemoteSelect'
import { formatCarNumber } from '@/common'
import { searchSubjects, searchSeries } from '@/axios'

export default {
  mixins: [DialogMixin],
  components: { DateTime, RemoteSelect },
  data: () => ({
    searchSeries,
    searchSubjects
  }),
  computed: {
    carNumber: {
      get () {
        return formatCarNumber(this.item.carNumber)
      },
      set (val) {
        this.$set(this.item, 'carNumber', val.replace(/\D/g, ''))
      }
    }
  }
}
</script>
