import Axios from 'axios'
import store from '@/store'

const keyConverter = {
  special: [
    { cs: 'ICO', js: 'ico' }
  ],
  deCSharpKeys (data) {
    if (data instanceof Array) {
      return data.map(val => this.deCSharpKeys(val))
    } else if (data instanceof Object) {
      const fixed = {}
      Object.keys(data).forEach(csKey => {
        const special = this.special.find(special => special.cs === csKey)
        const jsKey = special
          ? special.js
          : `${csKey.slice(0, 1).toLowerCase()}${csKey.slice(1)}`
        fixed[jsKey] = this.deCSharpKeys(data[csKey])
      })
      return fixed
    } else {
      return data
    }
  },
  reCSharpKeys (data) {
    if (data instanceof Array) {
      return data.map(val => this.reCSharpKeys(val))
    } else if (data instanceof Object) {
      const fixed = {}
      Object.keys(data).forEach(jsKey => {
        const special = this.special.find(special => special.js === jsKey)
        const csKey = special
          ? special.cs
          : `${jsKey.slice(0, 1).toUpperCase()}${jsKey.slice(1)}`
        fixed[csKey] = this.reCSharpKeys(data[jsKey])
      })
      return fixed
    } else {
      return data
    }
  }
}
const deCSharpKeys = keyConverter.deCSharpKeys.bind(keyConverter)
const reCSharpKeys = keyConverter.reCSharpKeys.bind(keyConverter)

async function download (config) {
  try {
    store.commit('clearAlert')
    return await axios(config)
  } catch (error) {
    store.commit('setAlert', {
      type: 'error',
      text: error.message
    })
    throw error
  }
}

const axios = Axios.create({
  baseURL: 'https://inpro-evidencenaklvozu.azurewebsites.net/API/'
  // baseURL: 'http://localhost:5000/API/'
})

axios.interceptors.response.use((response) => {
  return {
    ...response,
    data: deCSharpKeys(response.data)
  }
}, error => {
  return Promise.reject(error)
})

export { deCSharpKeys, reCSharpKeys, download, axios }
